<template>
	<div class="chapters">
		<van-nav-bar :title="bookInfo.title" left-arrow @click-left="$router.back()">
			<template #right>
				<van-icon name="search" size="18" />
			</template>
		</van-nav-bar>
		<div class="chapterNav-tab">
			<div class="group">
				<h3 class="cell">
					目录
				</h3>
			</div>
		</div>
		<div class="catelogX" v-if="chapterlist">
			<div class="header">
				<div class="header-l">
					<h4>共{{chapterlist.length}}章</h4>
				</div>
				<div class="header-r">
					<span @click="show">
						<span v-if="flag">倒序</span>
						<span v-else>正序</span>
					</span>
				</div>
			</div>
			<van-divider content-position="left">正文</van-divider>
			<ul class="titlelist">
				<li class="titlelist-item" v-for="(item,index) in chapterlist" :key="index" @click="abc(item.link)">
					<span>{{item.title}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import {
		mapState
	} from "vuex";
	export default {
		data() {
			return {
				chapterlist: null,
				flag: true,
			};
		},
		computed: {
			...mapState(["chapter", "bookInfo"]),
		},
		created() {
			if (this.chapter) {
				this.chapterlist = this.chapter.chapters;
			}
		},
		methods: {
			abc(e) {
				this.$store.commit('getLinks', e);
				this.$router.push('/book/' + this.$route.params.id + '/read');
			},
			show() {
				this.flag = !this.flag;
				this.chapterlist.reverse();
			},
		}
	};
</script>

<style lang="less">
	.chapters {
		.van-nav-bar {
			.van-nav-bar__content {
				.van-icon {
					color: #000;
				}

				.van-nav-bar__title {
					font-size: 15px;
					margin: 0 45px;
				}
			}
		}

		.chapterNav-tab {
			position: relative;
			background-color: #fff;

			.group {
				display: table;
				width: 100%;

				.cell {
					font-size: 14px;
					font-weight: 400;
					display: table-cell;
					margin: 0;
					color: #ed424b;
					border-bottom: 1px solid;
					border-bottom-color: #ed424b;
					text-align: center;
					line-height: 45px;
				}
			}
		}

		.catelogX {
			.header {
				line-height: 28px;
				height: 28px;
				padding: 8px 16px;
				border-top: 0;
				background-color: #fff;

				.header-l {
					float: left;

					h4 {
						margin: 0;
						font-size: 15px;
						font-weight: 700;
					}
				}
				.header-r{
					float: right;
					span{
						color: #33373d;
						font-size: 14px;
					}
				}
				&::after {
					display: table;
					clear: both;
					content: '';
				}
			}

			.van-divider {
				margin: 5px 0;
				font-size: 13px;
			}

			.van-divider--content-left::before {
				max-width: 0%;
			}

			.titlelist {
				border-bottom: 1px solid #f0f1f2;
				background-color: #fff;

				.titlelist-item {
					span {
						font-size: 14px;
						padding: 12px 16px;
						padding-right: 32px;
						white-space: nowrap;
						text-overflow: ellipsis;
						overflow: hidden;
						display: block;
					}

					&::after {
						border-bottom: none;
						border-top: 1px solid #f0f1f2;
					}
				}
			}
		}
	}
</style>
